<template>
	<div class="carousel-wrap" id="carousel">
		<transition-group tag='ul' class='slide-ul' name='list'>
			<li v-for="(list,index) in slideList" @mouseenter="stop" @mouseleave="go" :key='index' v-show="index===currentIndex">
				<a :href="list.clickUrl">
					<img :src="list.image" :alt="list.desc"/>
				</a>
			</li>
		</transition-group>
		<div class="carousel-items">
			<span v-for="(item,index) in slideList.length" @mouseover="change(index)" :class="{'active':index===currentIndex}">

			</span>
		</div>
	</div>
</template>

<script>
	import Img_p1 from '../assets/img/p1.png'
	import Img_p2 from '../assets/img/p2.png'
	import Img_p3 from '../assets/img/p3.png'
	
	export default{
		data(){
			return{
				slideList:[
					{
						'clickUrl' : '#',
						'desc' : '第一张图',
						'image' : Img_p1
					},
					{
						'clickUrl' : '#',
						'desc' : '第二张图',
						'image' : Img_p2
					},
					{
						'clickUrl' : '#',
						'desc' : '第三张图',
						'image' : Img_p3
					}
				],
				currentIndex : 0,
				timer : ''
			}
		},
		methods:{
			go(){
				this.timer = setInterval(() => {
					this.autoPlay()
				},4000)
			},
			stop(){
				clearInterval(this.timer)
				this.timer = null
			},
			change(index){
				this.currentIndex = index
			},
			autoPlay(){
				this.currentIndex++
				if(this.currentIndex > this.slideList.length - 1){
					this.currentIndex = 0
				}
			}
		},
		created(){
			this.$nextTick(() => {
				this.timer = setInterval(() => {
					this.autoPlay()
				},4000)
			})
		}
	}
</script>

<style>
	 .carousel-wrap {
	   height: 453px;
	   width: 100%;
	   overflow: hidden;
	   background-color: #fff;
	   border-radius: 10px;
	}
	
	.slide-ul {
	  position: relative;
	  width: 100%;
	  height: 100%;
	}
	
	.slide-ul li {
	  position: absolute;
	  width: 100%;
	  height: 100%;
	}
	
	img {
	  width: 100%;
	  height: 100%;
	}
	
	.carousel-items {
	  position: absolute;
	  z-index: 10;
	  top: 380px;
	  width: 100%;
	  margin: 0 auto;
	  text-align: center;
	  font-size: 0;
	}
	
	.carousel-items span {
	  display: inline-block;
	  height: 6px;
	  width: 30px;
	  margin: 0 3px;
	  background-color: #b2b2b2;
	  cursor: pointer;
	}
	.carousel-items .active {
	  background-color: orange;
	}
	
	.list-enter-to {
	  transition: all 1s ease;
	  transform: translateX(0);
	}
	
	.list-leave-active {
	  transition: all 1s ease;
	  transform: translateX(-100%)
	}
	
	.list-enter {
	  transform: translateX(100%)
	}
	
	.list-leave {
	  transform: translateX(0)
	}
</style>



